footer {
    background: #333;
    color: #fff;
    padding: 6rem;
    text-align: center;
}
footer a {
    color: #fff;
}
footer ul {
    margin: 0;
    padding: 0;
}
footer li {
    display: inline-block;
    margin: 0;
}
footer li:before {
    content: "\2022 \A0";
}
footer li:first-child:before {
    content: "";
}
/* Custom styling for the content */
.rocky-image {
    width: 100%;
    max-width: 54rem;
}
.badge {
    border-radius: 50%;
    background: green;
    display: flex;
    flex-shrink: 0;
    height: 10rem;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    min-width: 10rem;
    width: 10rem;
    position: relative;
    box-shadow: 0 .2rem .1rem .1rem rgba(0,0,0,0.2);
}
.badge:before {
    border-radius: 50%;
    content: "";
    position: absolute;
    top: 2.5px;
    right: 2.5px;
    bottom: 2.5px;
    left: 2.5px;
    border: 6px solid #fff;
}
.badge.first {
    background: red;
}
.badge.third {
    background: yellow;
}
.rocky-badge {
    height: 70px;
    z-index: 10;
}
.rocky-image-small {
    width: 150%;
    margin-left: -2rem;
}
.calls-to-action {
    margin: 8rem 0 16rem;
}
.calls-to-action h2 {
    margin-bottom: 2rem;
}
.calls-to-action .col {
    min-height: 32rem;
    margin-top: 2rem;
    text-align: center;
}
.calls-to-action .col p:nth-child(2) {
    flex-grow: 1;
}
.calls-to-action .col.most-popular {
    background: #fff;
    border: 2px solid rgba(0,0,0,0.2);
    border-radius: .8rem;
    margin-top: 4em;
    transform: scale(1.2);
}
.section-title {
    text-align: center;
}
.button {
    background: #269300;
    border-radius: .8rem;
    color: #fff;
    display: block;
    padding: 2rem;
    margin: 0 auto;
    text-align: center;
    width: 20rem;
}
header {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 18px;
    height: 100vh;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    overflow: hidden;
    position: relative;
    text-align: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  
    -webkit-perspective: 100px;
  
            perspective: 100px;
  }
  
  header:before {
    -webkit-animation: fade-slide-down 2s .5s cubic-bezier(0, 0.5, 0, 1) forwards;
            animation: fade-slide-down 2s .5s cubic-bezier(0, 0.5, 0, 1) forwards;
    background: -webkit-linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,.8)),
                url(../../../public/background.jpg) no-repeat bottom;
    background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.8)),
                url(../../../public/background.jpg) no-repeat bottom;
    background-size: cover;
      bottom: 0;
    content: "";    
      left: 0;
    opacity: 0;
    position: absolute;
      right: 0;
      top: 0;
    z-index: -1;
  }
  
  header:after {
    background: #F9FCFF;
    content: "";
    height: 40rem;
    left: -5%;
    position: absolute;
      right: -5%;
      top: 90%;
    -webkit-transform: rotateZ(-4deg);
            transform: rotateZ(-4deg);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    z-index: 0;
  }
  
  .header-title, .header-subtitle {
    color: #fff;
  }
  
  .header-subtitle {
    margin-bottom: 5rem;
    text-transform: uppercase;
  }
  
  .header-button {
    position: relative;
    -webkit-transform: translateZ(.1px);
            transform: translateZ(.1px);
    z-index: 10;
  }
  
  /* Animations */
  
  @-webkit-keyframes fade-slide-down {
    0% {
      opacity: 0;
      -webkit-transform: translateY(-4rem);
              transform: translateY(-4rem);
    }
    100% {
      opacity: 1;
      -webkit-transform: none;
              transform: none;
    }
  }
  
  @keyframes fade-slide-down {
    0% {
      opacity: 0;
      -webkit-transform: translateY(-4rem);
              transform: translateY(-4rem);
    }
    100% {
      opacity: 1;
      -webkit-transform: none;
              transform: none;
    }
  }
  
  